function NextSlide(props) {
 
  const paused=useSelector(state=>state.paused);
  const [roundData, setRoundData] = useState(null);
  const [pausedCss1,SetpausedCss1]=useState(null);
  const [pausedCss3,SetpausedCss3]=useState(styles.hide);
 
  const num = useSelector(state => state.num);
  const bet = useSelector(state => state.bet);
  const userAddress=useSelector(state=>state.userAddress);
  const [betedCss,setBetedCss]=useState(styles.hide);
  const [betedCss2,setBetedCss2]=useState(styles.hide);
  const [bettinCss,setBettingCss]=useState(null);
  const [str,setStr]=useState("");
  const [claim1, setClaim1] = useState(null);
  
  var mycss = props.mycss;
  var currentIcon = props.currentIcon
  useEffect(() => {

    const interval = setInterval(() => {

       getRoundData(num).then((r)=>{
    
        console.log(r);
 setRoundData(r);
 
       }) 
    }, 10000);
    return () => clearInterval(interval);
  },[num]);
  useEffect(() => {
    if(paused==true)
    {
      SetpausedCss3(null);
      SetpausedCss1(styles.hide);
    }
   else{
    SetpausedCss3(styles.hide);
    SetpausedCss1(null);

     f();
   }
    async function f() {
      if(num>0)
      var r = await getRoundData(num)
 
      setRoundData(r);
     
      if(userAddress!=null)
      {
      var obj=await getUserBet(userAddress,num);
      
      if(obj.position==1 && obj.amount>0)
      {
        setBetedCss(styles.button5);
        setBetedCss2(null);
        setBettingCss(styles.hide);
        setStr("Down Entered")
        setClaim1(obj.amount)
      }
      else  if(obj.position==0 && obj.amount>0)
      {
        setBetedCss2(null);
        setBetedCss(styles.button5);
        setStr("UP Entered")
        setBettingCss(styles.hide);
        setClaim1(obj.amount)
      }
      else if(obj.amount==0)
      {
        setBetedCss2(styles.hide);
        setBetedCss(styles.hide);
        setBettingCss(null);
      }
      }
      else{
        setStr("")
        setBetedCss(styles.hide);
        setBettingCss(null);
      }

    }
    
  },[num,bet,userAddress,paused]);
  
  return (
    <div>

      <div className={styles.center,styles.zoomin2}>
        <div className={styles.box1}>
          <div className={classnames(styles.row1)}>
            <div className={classnames(styles.col1, "font_24", "font_5_s", "font_26_m")}>
              <div className={styles.live}>Next</div>
            </div>
            <div className={classnames(styles.col2, "font_5", "font_5_s", "font_26_m")}>
              <div className={styles.three}>#{num}</div>
            </div>
            <div className={styles.col3}>
              <img src={currentIcon} className={styles.icon1}></img>
            </div>
          </div>
          <hr className={styles.hr}></hr>
          {/*<div className={styles.row2}>
            &nbsp;&nbsp;
  </div>*/}
        <div className={classnames(styles.box3,styles.uptr)}>
          <div className={styles.inner}>
            <div className={classnames(styles.up, "font_5", "font_23_s", "font_17_m")}>UP</div>
            <div className={classnames(styles.x2, "font_21", "font_4_s", "font_27_m")}>X{roundData?.payoutUp}</div>
          </div></div>
          <div className={classnames(styles.box23,pausedCss3)}><img src={paused2} className={styles.paused3}></img></div>
          <div className={classnames(styles.box2,pausedCss1)}>
           <button className={classnames(betedCss,"font_5", "font_23_s", "font_28_m")}>{str}</button>
           <div className={classnames(styles.pr,betedCss2)}><span className={styles.pr2}>Prize Pool:</span><span className={styles.pr3}>{roundData?.totalAmount}</span></div>
           <div className={classnames(betedCss2,styles.my)}><span className={styles.my2}>My Bet:</span><span className={styles.my3}>{claim1}</span></div>
            <div className={classnames(styles.marg, "font_24",bettinCss)}><BetAmount label="Enter Up" currentIcon={currentIcon}/></div>
            <div className={classnames(styles.marg2, "font_24",bettinCss)}><BetAmount label="Enter Down" currentIcon={currentIcon} /></div>

          </div>
          <div className={classnames(styles.box4,styles.downtr)}>
          <div className={styles.inner2}>
            <div className={classnames(styles.x1, "font_21", "font_4_s", "font_27_m")}>X{roundData?.payoutDown}</div>
            <div className={classnames(styles.down, "font_5", "font_23_s", "font_17_m")}>Down</div>
          </div></div>

        </div>
      </div>

    </div>

  );
}
