export const getUserHistory = async (_predictionIndex, _userAddress) => {
    const predictionAddress = await getPredictionAddress(_predictionIndex);
    const apiURL = apiBase + "userHistory" + apiId + `&user=${_userAddress}&prediction=${predictionAddress}`;
    const data = await fetch(apiURL);
    const history = await data.json();
    const rounds = history.result;

    const oracleAddress = await getOracle(_predictionIndex);
    let geckoAPIid = oracleAddresses[oracleAddress].geckoAPIid;
    let geckoAPIcurrency = oracleAddresses[oracleAddress].geckoAPIcurrency;
    const tokenName = oracleAddresses[oracleAddress].tokenName;
    const priceRes = await fetch(`https://api.coingecko.com/api/v3/simple/price?ids=${geckoAPIid}&vs_currencies=${geckoAPIcurrency}`);
    const priceData = await priceRes.json();
    const tokenUSDPrice = priceData[geckoAPIid][geckoAPIcurrency];

    const bettingTokenAddress = await getPredictionBettingTokenAddress(_predictionIndex);
    const bettingTokenName = tokenContracts[bettingTokenAddress].symbol;
    const bettingGeckoAPIid = tokenContracts[bettingTokenAddress].geckoid;
    const bettingPriceRes = await fetch(`https://api.coingecko.com/api/v3/simple/price?ids=${bettingGeckoAPIid}&vs_currencies=usd`);
    const bettingPriceData = await bettingPriceRes.json();
    const bettingTokenUSDPrice = bettingPriceData[bettingGeckoAPIid]['usd'];

    const prediction = await getPredictionContract(_predictionIndex);    
    //const currentRoundNo = await prediction.methods.currentEpoch().call();

    let netEarning = new BigNumber(0);
    let avgReturn = new BigNumber(0);
    let bestRound = new BigNumber(0);
    let avgBet = new BigNumber(0);
    let totalBet = new BigNumber(0);

    let won = 0, lost = 0, skipCurrentRoundCount = 0;
    let roundsFiltered = [];
    for(let i =0; i< rounds.length; i++){
        const round = rounds[i];
        round.isRefundable = false;

        //check uncalculated rounds and set as refundable if the rounds are already over
        if(!round.calculated) {
            const refundable = await prediction.methods.refundable(round.currentEpoch, _userAddress).call();
            if(refundable) round.isRefundable = true;
            else continue;
        }
        
        round.roundNo = round.currentEpoch;
        totalBet = totalBet.plus(round.amount);
        if(round.won)
        {
            won++;
            const profit = BigNumber(round.amountWon).plus(round.refereeAmount).plus(round.stakingAmount).minus(round.amount);
            round.profit = profit;
            netEarning = netEarning.plus(profit);
            if(profit > bestRound) bestRound = profit;
        }
        else
        {
            lost++;
            round.profit = -round.amount;
            netEarning = netEarning.minus(round.amount);
        } 
        round.amount = BigNumber(round.amount).dividedBy(BIG_TEN.pow(18)).toString();
        round.refereeAmount = BigNumber(round.refereeAmount).dividedBy(BIG_TEN.pow(18)).toString();
        round.amountWon = BigNumber(round.amountWon).dividedBy(BIG_TEN.pow(18)).toString();
        round.profit = BigNumber(round.profit).dividedBy(BIG_TEN.pow(18)).toString();
        //to be deleted
        round.priceDiff = round.profit;
        roundsFiltered.push(round);
    }
    
    
    avgBet = totalBet.dividedBy(rounds.length).dividedBy(BIG_TEN.pow(18)).toString();
    avgReturn = netEarning.dividedBy(rounds.length).dividedBy(BIG_TEN.pow(18)).toString();
    netEarning = netEarning.dividedBy(BIG_TEN.pow(18)).toString();
    bestRound = bestRound.dividedBy(BIG_TEN.pow(18)).toString();
    totalBet = totalBet.dividedBy(BIG_TEN.pow(18)).toString();

    const result = {numRounds:roundsFiltered.length, won, lost, bettingTokenName, bettingTokenUSDPrice, tokenName, tokenUSDPrice, netEarning, avgReturn, bestRound, avgBet, totalBet, rounds:roundsFiltered};
    return result;
}


export const getUserClaimableRounds = async (_predictionIndex, _userAddress) => {
    const predictionAddress = await getPredictionAddress(_predictionIndex);
    const prediction = await getPredictionContract(_predictionIndex);

    const apiURL = apiBase + "userWon" + apiId + `&user=${_userAddress}&prediction=${predictionAddress}`;
    const data = await fetch(apiURL);
    const history = await data.json();
    const rounds = history.result;

    const bettingTokenAddress = await getPredictionBettingTokenAddress(_predictionIndex);
    const bettingTokenName = tokenContracts[bettingTokenAddress].symbol;
    const bettingGeckoAPIid = tokenContracts[bettingTokenAddress].geckoid;
    const bettingPriceRes = await fetch(`https://api.coingecko.com/api/v3/simple/price?ids=${bettingGeckoAPIid}&vs_currencies=usd`);
    const bettingPriceData = await bettingPriceRes.json();
    const bettingTokenUSDPrice = bettingPriceData[bettingGeckoAPIid]['usd'];

    let totalAmount = new BigNumber('0');
    let claimableRounds = [];
    let roundData = [];

    for(let i =0; i< rounds.length; i++){
        const round = rounds[i];
        if(round.calculated && round.won)
        {
            claimableRounds.push(parseInt(round.currentEpoch));
            totalAmount = totalAmount.plus(round.amountWon).plus(round.refereeAmount).plus(round.stakingAmount);
            round.isRefundable = false;
            roundData.push(round);
        }
        else
        {
            const refundable = await prediction.methods.refundable(round.currentEpoch, _userAddress).call();
            if(refundable)
            {
                claimableRounds.push(parseInt(round.currentEpoch));
                totalAmount = totalAmount.plus(round.amountWon).plus(round.refereeAmount).plus(round.stakingAmount);
                round.isRefundable = true;
                roundData.push(round);
            }
        } 
    }
    totalAmount = BigNumber(totalAmount).dividedBy(BIG_TEN.pow(18)).toString();
    const totalUsdAmount = BigNumber(totalAmount).multipliedBy(bettingTokenUSDPrice).toString();
    const result = {totalUsdAmount, totalAmount, claimableRounds, roundData};
    return result;
}

export const getAllUserClaimableRounds = async (_userAddress) => {
    const numPredictions = await getPredictionCount();
    const result = {totalUsdAmount:new BigNumber('0'), predictionIds:[], predictionRounds:[], predictions:[]};
    for(let i=0; i<numPredictions; i++){
        let data = await getUserClaimableRounds(i, _userAddress);

        if(data.claimableRounds.length > 0)
        {
            result.totalUsdAmount = result.totalUsdAmount.plus(BigNumber(data.totalUsdAmount));
            const predictionData = {predictionId:i, rounds:data.claimableRounds};
            result.predictions.push(predictionData);
            result.predictionIds.push(i);
            result.predictionRounds.push(data.claimableRounds);
        }
    }

    console.log(result);
    return result;
}

export const getUserBet = async (userAddress, roundNo) => {
    if(roundNo < 0) return {position:0,amount:0,refereeAmount:0,referrerAmount:0,stakingAmount:0,claimed:false};
    const ledger = await predictionContract.methods.ledger(roundNo, userAddress).call();
    const decimal = await getBettingTokenDecimal();
    ledger.amount = (new BigNumber(ledger.amount).dividedBy(BIG_TEN.pow(decimal))).toString();
    ledger.refereeAmount = (new BigNumber(ledger.refereeAmount).dividedBy(BIG_TEN.pow(decimal))).toString();
    ledger.referrerAmount = (new BigNumber(ledger.referrerAmount).dividedBy(BIG_TEN.pow(decimal))).toString();
    ledger.stakingAmount = (new BigNumber(ledger.stakingAmount).dividedBy(BIG_TEN.pow(decimal))).toString();
    return ledger;
}

