export const getTotalClaimableReferenceBonus = async (publicKey) => {
    const numPredictions = await getPredictionCount();
    let result = {totalBonus: new BigNumber(0), predictionIds:[]}
    for(let i=0; i<numPredictions; i++){
        let bonus = await getClaimableReferenceBonusOfPredinction(publicKey, i);
        if(BigNumber(bonus) > 0) {
            result.totalBonus = result.totalBonus.plus(bonus);
            result.predictionIds.push(i);
        }
    }
    console.log(result);
    return result;
}

