export const addReference = async (web3Wallet, publicKey, referenceCode) => {
    if(!web3.utils.isAddress(referenceCode)){
        console.log('invalid code');
        return false;
    }
    const codeEnabled = await hasApprovedReferralSystem(referenceCode);
    if(!codeEnabled){
        console.log('invalid code - not enabled');
        return false;
    }
    const referralContract = new web3Wallet.eth.Contract(referralABI, referralContractAddress);
    let err = null;
    await referralContract.methods.setReferrer(referenceCode).send({from:publicKey})
    .catch((error) => {
        err = error;
        console.log(error.message);
        return false;
    });
    return err == null;
}

